*{
  --darkmode: #36393f;
  text-decoration: none;
  --header: #23272a;
}

body{
  background-color: var(--darkmode);
}

.header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-height: 10vh;
  background-color: var(--header);
}
.header > .title{
  display: flex;
  align-items: center;
  gap: 10px;
  
}
.header > div > img {
  max-height: 5rem;
}

nav{
  display: flex;
  justify-content: space-around;
  gap: 10px;
  align-items: center;
  color: antiquewhite;
}

h1, h3{
  color: #ffffff;
}

.product-container{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

.product-box{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.cart-page{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.cart-header{
  font-size: 6rem;
  color: white;
}

.cart-item{
  display: flex;
  padding: 20px;
  justify-content: space-between;
  background-color: aliceblue;
  width: 90vw;
}
.cart-item > div > div > h1 {
  color: black;
}

.cart-item > div > img {
  height:250px;
  width: auto;
  border-radius: 25px;
}

.cart-item-info{
  display: flex;
  flex-direction: column;
  padding: 20px;
}

.cart-item > .left-page {
  display: flex
}

.nutrition-page{
  background-color: white;
}